@import url(https://fonts.googleapis.com/css?family=Nanum+Myeongjo);
.font-title {
  font-size: 1.2em;
}

.font-body-1 {
  font-size: 1em;
}

.font-body-2 {
  font-size: 0.8em;
}

.font-message {
  font-size: 0.9em;
}

html, body {
  height: 100%;
  font-family: 'Nanum Myeongjo', serif;
  color: #333;
}

#root {
  height: 100%;
}

.panel {
  min-height: 100%;
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  text-align: center;
}

.img {
  text-align: center;
  background-color: #ffffff;
  width: 60%;
  /* margin: auto; */
}

.img img {
  width: 100%;
  vertical-align: bottom;
  margin-top: 12px;
}

.flower {
  width: 60px;
}

.panel1 {
  /* font-size: 1.2em; */
  line-height: 1.8;
  word-break: keep-all;
  white-space: nowrap;
}

.panel1-lower {
  /* font-size: 0.8em; */
}

.panel2 {
  /* font-size: 0.8em; */
  line-height: 3;
  word-break: keep-all;
  white-space: nowrap;
}

.section {
  text-align: center;
  margin-top: 70px;
}

.map {
  display: flex;
  justify-content: center;
}

.contact-section {
  margin-bottom: 30px;
  color: #888;
  width: 100%;
}

.name {
  color: #000;
}

.panel-contact .title {
  margin-bottom: 32px;
}

.contact {
  text-align: center;
  margin: 8px 0px;
}

.contact .sms {
  margin-left: 10px;
}

.contact img {
  width: 42px;
  opacity: 0.7;
}

.contact-main {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.contact-sub + .contact-sub {
  margin-top: 10px;
}

.row {
  display: flex;
  word-break: keep-all;
  white-space: nowrap;
}

.col {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.message-container {
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

ul, li {
  list-style: none;
  list-style-image: none;
  margin: 0;
  padding: 0;
}

.message-form {
  margin: 32px 0px;
}

.message-form li {
  border-bottom: 1px solid #e0dcd3;
  min-height: 30px;
  position: relative;
  padding-left: 80px;
  line-height: 30px;
  padding-top: 5px;
  padding-bottom: 5px;
}

.message-form input {
  background: none;
  border: none;
  width: 100%;
}

.message-form .btn {
  margin-top: 10px;
}

.tt {
  display: block;
  position: absolute;
  left: 0px;
}

.btn {
  color: #fff;
  font-weight: bold;
  display: block;
  box-sizing: border-box;
  background: #232323;
  padding: 10px;
  min-height: 20px;
  line-height: 20px;
  width: 100%;
  text-decoration: none;
}

.messages {
  margin: 30px 0;
}

.message-title {
  margin-top: 80px;
}

.message {
  text-align: left;
  margin: 8px 0;
  padding: 10px;
  border: 1px solid #ccc;
  /* font-size: 0.9em; */
}

.message .date {
  font-size: 0.7em;
  float: right;
}

.message .name {
  font-weight: 700;
}

.message .body {
  margin-top: 16px;
  line-height: 1.5;
}

.disabled {
  opacity: 0.5;
}

.naver-map {
  /* padding: 10px; */
}

.naver-map .map {
  height: 250px;
}

.naver-map .info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  text-align: left;
}

.naver-map .title {
  font-weight: 600;
  /* font-size: 1.2em; */
}

.icon img {
  width: 42px;
}

.naver-map .desc {
  margin-top: 10px;
  line-height: 1.2;
}

.naver-map .phone {
  margin-top: 10px;
}

